/* Dots */
.slick-slider
{
    margin-bottom: 30px;
}

.slick-dots
{
    position: absolute;
    bottom: -45px;

    display: block;

    width: 100%;
    padding: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li
{
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
    opacity: 1;
}
.slick-dots li button:before
{
    font-family: 'slick';
    font-size: 0;
    line-height: 20px;
    position: absolute;
    top: 0px;
    left: 0;
    width: 20px;
    height: 20px;
    content: '';
    text-align: center;
    opacity: .25;
    -ms-filter: "alpha(opacity=25)";  /* ie 8 */
    filter: alpha(opacity=25);        /* ie lt 8 */
    color: black;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -ms-filter: "alpha(opacity=25)";
    filter: alpha(opacity=25);        /* ie lt 8 */
    width: 8px;
    height: 8px;
    background: #ccc;
    border-radius: 10px;
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    -ms-filter: "alpha(opacity=75)";  /* ie 8 */
    filter: alpha(opacity=70);        /* ie lt 8 */
    color: black;
}